/* eslint no-console:0 */
import React, { PropTypes } from 'react';
import classNames from 'classnames';
import TreeNode from './TreeNode';
import {
  loopAllChildren, isInclude,
  filterParentPosition, handleCheckState, getCheck,
  getStrictlyValue, arraysEqual,
} from './util';
import style from './Tree.css';

function noop() {
}

function getDefaultCheckedKeys(props, willReceiveProps) {
  let checkedKeys = willReceiveProps ? undefined : props.defaultCheckedKeys;
  if ('checkedKeys' in props) {
    checkedKeys = props.checkedKeys || [];
    if (props.checkStrictly) {
      if (props.checkedKeys.checked) {
        checkedKeys = props.checkedKeys.checked;
      } else if (!Array.isArray(props.checkedKeys)) {
        checkedKeys = [];
      }
    }
  }
  return checkedKeys;
}

function getFilterExpandedKeys(props, expandKeyProp, expandAll) {
  const keys = props[expandKeyProp];
  if (!expandAll && !props.autoExpandParent) {
    return keys || [];
  }
  const expandedPositionArr = [];
  if (props.autoExpandParent) {
    loopAllChildren(props.children, (item, index, pos, newKey) => {
      if (keys.indexOf(newKey) > -1) {
        expandedPositionArr.push(pos);
      }
    });
  }
  const filterExpandedKeys = [];
  loopAllChildren(props.children, (item, index, pos, newKey) => {
    if (expandAll) {
      filterExpandedKeys.push(newKey);
    } else if (props.autoExpandParent) {
      expandedPositionArr.forEach((p) => {
        if (
          (
            (
              p.split('-').length > pos.split('-').length &&
              isInclude(pos.split('-'), p.split('-'))
            ) ||
            pos === p
          ) &&
          filterExpandedKeys.indexOf(newKey) === -1
        ) {
          filterExpandedKeys.push(newKey);
        }
      });
    }
  });
  return filterExpandedKeys.length ? filterExpandedKeys : keys;
}

function getDefaultExpandedKeys(props, willReceiveProps) {
  let expandedKeys = willReceiveProps ? undefined :
    getFilterExpandedKeys(props, 'defaultExpandedKeys',
      props.defaultExpandedKeys.length ? false : props.defaultExpandAll);
  if ('expandedKeys' in props) {
    expandedKeys = (props.autoExpandParent ?
      getFilterExpandedKeys(props, 'expandedKeys', false) :
      props.expandedKeys) || [];
  }
  return expandedKeys;
}

class Tree extends React.Component {
  constructor(props) {
    super(props);
    ['onCheck'].forEach((m) => {
      this[m] = this[m].bind(this);
    });
    this.checkedKeysChange = true;

    this.state = {
      expandedKeys: getDefaultExpandedKeys(props),
      checkedKeys: getDefaultCheckedKeys(props),
    };
  }

  componentWillReceiveProps(nextProps) {
    const expandedKeys = getDefaultExpandedKeys(nextProps, true);
    const checkedKeys = getDefaultCheckedKeys(nextProps, true);
    const st = {};
    if (expandedKeys) {
      st.expandedKeys = expandedKeys;
    }
    if (checkedKeys) {
      if (nextProps.checkedKeys === this.props.checkedKeys) {
        this.checkedKeysChange = false;
      } else {
        this.checkedKeysChange = true;
      }
      st.checkedKeys = checkedKeys;
    }
    this.setState(st);
  }
  onExpand(treeNode) {
    const expanded = !treeNode.props.expanded;
    const controlled = 'expandedKeys' in this.props;
    const expandedKeys = [...this.state.expandedKeys];
    const index = expandedKeys.indexOf(treeNode.props.eventKey);
    if (expanded && index === -1) {
      expandedKeys.push(treeNode.props.eventKey);
    } else if (!expanded && index > -1) {
      expandedKeys.splice(index, 1);
    }
    if (!controlled) {
      this.setState({ expandedKeys });
    }
    this.props.onExpand(expandedKeys, { node: treeNode, expanded });

    // after data loaded, need set new expandedKeys
    if (expanded && this.props.loadData) {
      /**
       * @todo
       * previous implementation returns this promise, while not providing any return elsewhere
       * Since this `loadData` prop is not used in current version of code, this return has been
       * removed.
       *
       * If returning this promise is required, please also add return Promise.resolve(); elsewhere,
       * to ensure consistent returns.
       */
      this.props.loadData(treeNode).then(() => {
        if (!controlled) {
          this.setState({ expandedKeys });
        }
      });
    }
  }

  onCheck(treeNode) {
    let checked = !treeNode.props.checked;
    if (treeNode.props.halfChecked) {
      checked = true;
    }
    const key = treeNode.props.eventKey;
    let checkedKeys = [...this.state.checkedKeys];
    const index = checkedKeys.indexOf(key);

    const newSt = {
      event: 'check',
      node: treeNode,
      checked,
    };

    if (this.props.checkStrictly && ('checkedKeys' in this.props)) {
      if (checked && index === -1) {
        checkedKeys.push(key);
      }
      if (!checked && index > -1) {
        checkedKeys.splice(index, 1);
      }
      newSt.checkedNodes = [];
      loopAllChildren(this.props.children, (item, ind, pos, keyOrPos) => {
        if (checkedKeys.indexOf(keyOrPos) !== -1) {
          newSt.checkedNodes.push(item);
        }
      });
      this.props.onCheck(getStrictlyValue(checkedKeys, this.props.checkedKeys.halfChecked), newSt);
    } else {
      if (checked && index === -1) {
        this.treeNodesStates[treeNode.props.pos].checked = true;
        const checkedPositions = [];
        Object.keys(this.treeNodesStates).forEach((i) => {
          if (this.treeNodesStates[i].checked) {
            checkedPositions.push(i);
          }
        });
        handleCheckState(this.treeNodesStates, filterParentPosition(checkedPositions), true);
      }
      if (!checked) {
        this.treeNodesStates[treeNode.props.pos].checked = false;
        this.treeNodesStates[treeNode.props.pos].halfChecked = false;
        handleCheckState(this.treeNodesStates, [treeNode.props.pos], false);
      }
      const checkKeys = getCheck(this.treeNodesStates);
      newSt.checkedNodes = checkKeys.checkedNodes;
      newSt.checkedNodesPositions = checkKeys.checkedNodesPositions;
      newSt.halfCheckedKeys = checkKeys.halfCheckedKeys;
      this.checkKeys = checkKeys;

      this.underscoreCheckedKeys = checkedKeys = checkKeys.checkedKeys;
      if (!('checkedKeys' in this.props)) {
        this.setState({
          checkedKeys,
        });
      }
      this.props.onCheck(checkedKeys, newSt);
    }
  }

  onSelect(id, value) {
    this.props.onSelect(id, value);
  }
  onDoubleClick(id, value) {
    this.props.onDoubleClick(id, value);
  }

  getRawExpandedKeys() {
    if (!this.rawExpandedKeys && ('expandedKeys' in this.props)) {
      this.rawExpandedKeys = [...this.state.expandedKeys];
    }
  }

  getOpenTransitionName() {
    const props = this.props;
    let transitionName = props.openTransitionName;
    const animationName = props.openAnimation;
    if (!transitionName && typeof animationName === 'string') {
      transitionName = `vm-tree-open-${animationName}`;
    }
    return transitionName;
  }

  filterTreeNode(treeNode) {
    const filterTreeNode = this.props.filterTreeNode;
    if (typeof filterTreeNode !== 'function' || treeNode.props.disabled) {
      return false;
    }
    return filterTreeNode.call(this, treeNode);
  }

  renderTreeNode(child, index, level = 0) {
    const pos = `${level}-${index}`;
    const key = child.key || pos;
    const state = this.state;
    const props = this.props;

    const cloneProps = {
      ref: `treeNode-${key}`,
      root: this,
      eventKey: key,
      pos,
      isShowIcon: props.isShowIcon,
      loadData: props.loadData,
      expanded: state.expandedKeys.indexOf(key) !== -1,
      openTransitionName: this.getOpenTransitionName(),
      openAnimation: props.openAnimation,
      filterTreeNode: this.filterTreeNode.bind(this),
    };
    if (props.checkable) {
      cloneProps.checkable = props.checkable;
      if (props.checkStrictly) {
        if (state.checkedKeys) {
          cloneProps.checked = state.checkedKeys.indexOf(key) !== -1 || false;
        }
        if (props.checkedKeys.halfChecked) {
          cloneProps.halfChecked = props.checkedKeys.halfChecked.indexOf(key) !== -1 || false;
        } else {
          cloneProps.halfChecked = false;
        }
      } else {
        if (this.checkedKeys) {
          cloneProps.checked = this.checkedKeys.indexOf(key) !== -1 || false;
        }
        cloneProps.halfChecked = this.halfCheckedKeys.indexOf(key) !== -1;
      }
    }
    if (this.treeNodesStates && this.treeNodesStates[pos]) {
      Object.assign(cloneProps, this.treeNodesStates[pos].siblingPosition);
    }
    return React.cloneElement(child, cloneProps);
  }

  render() {
    const props = this.props;
    const domProps = {
      className: classNames(props.className, style['vm-tree']),
      role: 'tree-node',
    };
    if (props.focusable) {
      domProps.tabIndex = '0';
      domProps.onKeyDown = (e) => { e.preventDefault(); };
    }
    const getTreeNodesStates = () => {
      this.treeNodesStates = {};
      loopAllChildren(props.children, (item, index, pos, keyOrPos, siblingPosition) => {
        this.treeNodesStates[pos] = {
          siblingPosition,
        };
      });
    };
    if (props.checkable && (this.checkedKeysChange || props.loadData)) {
      if (props.checkStrictly) {
        getTreeNodesStates();
      } else if (props.treeNodesStates) {
        this.treeNodesStates = props.treeNodesStates.treeNodesStates;
        this.halfCheckedKeys = props.treeNodesStates.halfCheckedKeys;
        this.checkedKeys = props.treeNodesStates.checkedKeys;
      } else {
        const checkedKeys = this.state.checkedKeys;
        let checkKeys;
        if (!props.loadData && this.checkKeys && this.underscoreCheckedKeys &&
          arraysEqual(this.underscoreCheckedKeys, checkedKeys)) {
          // if checkedKeys === underscoreCheckedKeys from onCheck, use underscoreCheckedKeys.
          checkKeys = this.checkKeys;
        } else {
          const checkedPositions = [];
          this.treeNodesStates = {};
          loopAllChildren(props.children, (item, index, pos, keyOrPos, siblingPosition) => {
            this.treeNodesStates[pos] = {
              node: item,
              key: keyOrPos,
              checked: false,
              halfChecked: false,
              siblingPosition,
            };
            if (checkedKeys.indexOf(keyOrPos) !== -1) {
              this.treeNodesStates[pos].checked = true;
              checkedPositions.push(pos);
            }
          });
          // if the parent node's key exists, it all children node will be checked
          handleCheckState(this.treeNodesStates, filterParentPosition(checkedPositions), true);
          checkKeys = getCheck(this.treeNodesStates);
        }
        this.halfCheckedKeys = checkKeys.halfCheckedKeys;
        this.checkedKeys = checkKeys.checkedKeys;
      }
    }

    return (
      <ul {...domProps} unselectable>
        {React.Children.map(props.children, this.renderTreeNode, this)}
      </ul>
    );
  }
}

Tree.propTypes = {
  isShowIcon: PropTypes.bool,
  children: PropTypes.oneOfType([
    PropTypes.element,
    PropTypes.arrayOf(PropTypes.element),
  ]),
  checkable: PropTypes.bool,
  treeNodesStates: PropTypes.shape({ }),
  checkStrictly: PropTypes.bool,
  autoExpandParent: PropTypes.bool,
  defaultExpandAll: PropTypes.bool,
  defaultExpandedKeys: PropTypes.arrayOf(PropTypes.string),
  expandedKeys: PropTypes.arrayOf(PropTypes.string),
  defaultCheckedKeys: PropTypes.arrayOf(PropTypes.string),
  checkedKeys: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.object,
  ]),
  onExpand: PropTypes.func,
  onCheck: PropTypes.func,
  onSelect: PropTypes.func,
  onDoubleClick: PropTypes.func,
  loadData: PropTypes.func,
  filterTreeNode: PropTypes.func,
  openTransitionName: PropTypes.string,
  openAnimation: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
};

Tree.defaultProps = {
  isShowIcon: true,
  checkable: false,
  checkStrictly: false,
  autoExpandParent: true,
  defaultExpandAll: false,
  defaultExpandedKeys: [],
  defaultCheckedKeys: [],
  onExpand: noop,
  onCheck: noop,
  onSelect: noop,
};
Tree.TreeNode = TreeNode;

Tree.examples = {
  DefaultExample: {
    description: 'CheckableTree Example',
    component: <Tree
      checkable defaultExpandAll
      defaultCheckedKeys={['0-0-0-0']}
      onSelect={value => console.log('selected', value)}
      onDoubleClick={value => console.log('selected', value)}
      onCheck={(checkedKeys, info) => console.log('onCheck', checkedKeys, info)}
    >
      <TreeNode title="parent 1">
        <TreeNode title="Leaf 1">
          <TreeNode title="Leaf 1" isLeaf />
        </TreeNode>
        <TreeNode title="Leaf 2" isLeaf />
      </TreeNode>
      <TreeNode title="parent 2" />
    </Tree>,
  },
  AExample: {
    description: 'Default Example',
    component: <Tree
      defaultExpandedKeys={['0-0']}
      onSelect={value => console.log('selected', value)}
      onDoubleClick={value => console.log('selected', value)}
    >
      <TreeNode title="parent 1">
        <TreeNode title="Leaf 1" >
          <TreeNode title="Leaf 1" value="value Leaf 1" tooltip isLeaf />
        </TreeNode>
        <TreeNode title="Leaf 2" isLeaf />
      </TreeNode>
      <TreeNode title="parent 2" isLeaf />
    </Tree>,
  },
};

export default Tree;



