import React from 'react';

const HighLight = ({ text, patt }) => {
  let result = patt.exec(text);
  let i = 0;
  const companents = [];
  while (result != null) {
    const temp = text.substring(i, result.index);
    companents.push(temp);
    companents.push(
      <span key={i} style={{ fontStyle: 'normal', background: '#ffff00' }}>{result[0]}</span>
    );
    i = patt.lastIndex;
    result = patt.exec(text);
  }
  if (i <= text.length) {
    companents.push(text.substring(i));
  }
  return (
    <div>
      {companents}
    </div>
  );
};

HighLight.propTypes = {
  text: React.PropTypes.string,
  patt: React.PropTypes.instanceOf(RegExp),
};

export default HighLight;



