import React, { PropTypes, Component } from 'react';
import Select from '../../Select/Select';
import Input from '../../Input/Input';
import { icons as enums } from '../../../constants/enums';
import Link from '../../Link/Link';
import Icon from '../../Icon/Icon';
import style from './salesforceLookup.css';
import Button from '../../Button/Button';
import { isNullOrEmpty } from '../../../utils/str';
import ModalStyle from '../Modal.css';

class SalesforceLookup extends Component {

  constructor(props) {
    super(props);
    this.state = {
      searchType: props.selectOptions ? props.selectOptions[0] : '',
      searchKeyWord: '',
      props: [],
      list: [],
      isRequesting: true,
      errorInfo: '',
    };
    this.searchTypeChange = this.searchTypeChange.bind(this);
    this.searchTextChanged = this.searchTextChanged.bind(this);
    this.updateList = this.updateList.bind(this);
    this.onKeyup = this.onKeyup.bind(this);
    this.searchHandler = this.searchHandler.bind(this);
    this.onKeyup = this.onKeyup.bind(this);
    this.searchHandler = this.searchHandler.bind(this);
  }


  componentDidMount() {
    this.props.onSalesforcelookUp((this.props.selectOptions ?
    this.props.selectOptions[0] : ''),
    '', this.updateList);
  }

  onKeyup(e) {
    if (e.keyCode === 13) this.searchHandler();
  }

  updateList(listInfo) {
    this.setState({ isRequesting: false });
    if (listInfo.errorCode === 0) {
      this.setState({ errorInfo: '',
        props: listInfo.props.filter(
        (item => (item.name !== 'id'))),
        list: listInfo.list });
    } else {
      this.setState({ errorInfo: listInfo.errorString });
    }
  }

  searchTextChanged(e) {
    this.setState({ searchKeyWord: e.target.value });
  }

  searchTypeChange(val) {
    this.setState({ searchType: val });
  }

  searchHandler() {
    this.setState({ isRequesting: true });
    this.props.onSalesforcelookUp(this.state.searchType, this.state.searchKeyWord, this.updateList);
  }

  render() {
    return (<div>
      <div className={style.title} >Lookup</div>
      <div className={style.lookupTool}>
        {
          this.props.selectOptions.length > 1 &&
          (<span className={style.lookupToolspan}>
            <Select
              input={{ onChange: this.searchTypeChange, value: this.state.searchType }}
              isDisabled={false}
              options={this.props.selectOptions.map(item => ({ text: item, value: item }))}
              width={100}
              dropdownClassName={ModalStyle.dropdown}
              customizeStyle={{ height: 30, top: 10 }}
            />
          </span>)
        }
        <span className={style.lookupToolspan}>
          <Icon
            type={enums.search}
            className={style.searchIcon}
            onClick={this.searchHandler}
          />
          <Input
            maxLength={512}
            type="text"
            className={style.searchInput}
            input={{ onChange: this.searchTextChanged, value: this.state.searchKeyWord }}
            meta={{}}
            onKeyUp={this.onKeyup}
            placeholder="search"
          />
        </span>
      </div>

      {this.state.isRequesting && (<div className={style.Loading}>
        <Icon type={enums.loading} /> Loading...
      </div>)}


      {(!this.state.isRequesting) && (isNullOrEmpty(this.state.errorInfo)) &&
          (this.state.list === null || this.state.list.length === 0) &&
          (<div className={style.noresult}>
         No resaults were found for your search.
          </div>)}


      {(!this.state.isRequesting) && (isNullOrEmpty(this.state.errorInfo)) &&
          (this.state.list !== null && this.state.list.length > 0) &&
          (<div>
            <div className={style.listtitle}>Search Resaults</div>
            <div className={style.infoList}>
              <div className={style.head}>
                {
                  this.state.props.map(item => (
                    <span key={item.name}>
                      {item.label}
                    </span>
                  ))
                }
              </div>
              <div className={style.resultList}>
                {this.state.list != null && this.state.list.length > 0 &&
                    this.state.list.map(
                      (item, idx) =>
                        (
                          <div key={idx} className={style.line}>
                            {this.state.props.map((label, i) => (
                              <span key={`${i} ${item[label.name]}`}>
                                {
                                  (label.name === 'name') ? (
                                    <Link
                                      onClick={() => {
                                        this.props.closeModal();
                                        this.props.onItemSelected(item.id, item[label.name]);
                                      }}
                                    >
                                      {item[label.name]}
                                    </Link>
                                  ) : (
                                    <lable>{item[label.name]}</lable>
                                  )
                                }
                              </span>
                            ))}
                          </div>
                        ))
                  }
              </div>
            </div>
            <div className={style.noteinfo}>
               Only 20 results are displayed at most.
            </div>
          </div>)}


      {!(this.state.isRequesting || isNullOrEmpty(this.state.errorInfo)) &&
          (<div className={style.lookupError}>
            <span>
              {`Error searching Salesforce records: ${this.state.errorInfo}`}
            </span>
          </div>)}
      <div className={style.cancelButton}>
        <Button text="Cancel" className="grayButton" onClick={() => { this.props.closeModal(); }} />
      </div>
    </div>);
  }
}

SalesforceLookup.propTypes = {
  closeModal: PropTypes.func,
  selectOptions: PropTypes.arrayOf(PropTypes.string).isRequired,
  onItemSelected: PropTypes.func,
  onSalesforcelookUp: PropTypes.func.isRequired,
};

export default SalesforceLookup;



