import React from 'react';
import * as _ from 'lodash';
import moment from 'moment';
import { fieldType } from '../../../constants/salesforce';
import { getDisplayText, convertSFDateToLocalDate, convertSFDateTimeToLocalDate, isSalesforceEmail, isSalesforceDouble, reduceArray, getInitValues, normalizeInt, normalizeDecimal } from './util';
import RenderField from './RenderField';
import style from './salesforce.css';
import Button from '../../Button/Button';
import * as localStorage from '../../../utils/localStorage';

const defaultValidate = (values, props) => {
  if (values) {
    return reduceArray(props.fields
    .filter(field => field.type !== fieldType.bool)
    .map((field) => {
      if (field.required) {
        if (field.type === fieldType.reference) {
          if (!values[field.systemName] ||
          (values[field.systemName] && !values[field.systemName].value)) {
            return { name: field.systemName, value: `${getDisplayText(field)} is required!` };
          }
        } else if (!values[field.systemName]) {
          return { name: field.systemName, value: `${getDisplayText(field)} is required!` };
        }
      } else if (values[field.systemName]) {
        if (field.type === fieldType.percent ||
        field.type === fieldType.currency || field.type === fieldType.double) {
          if (!isSalesforceDouble(values[field.systemName])) {
            return { name: field.systemName, value: 'Please enter a number.' };
          }
        } else if (field.type === fieldType.email) {
          if (!isSalesforceEmail(values[field.systemName])) {
            return { name: field.systemName, value: 'Incorrect email format.' };
          }
        }
      }
      /** @todo this implementation seems strange */
      return undefined;
    }));
  }
  return {};
};

class CustomForm extends React.Component {
  constructor(props) {
    super(props);
    const cacheState = localStorage.getObject('salesforceFormState');
    this.scrollTop = localStorage.getNumber('salesforceFormScrollTop');
    if (cacheState) {
      this.state = cacheState;
    } else {
      this.state = {
        values: getInitValues(props.fields, props.fieldsValues),
        errors: {},
      };
    }
    this.handleValueChange = this.handleValueChange.bind(this);
    this.onSubmitSalesforce = this.onSubmitSalesforce.bind(this);
    this.onCancelClick = this.onCancelClick.bind(this);
  }
  componentDidMount() {
    if (this.scrollTop) {
      this.formWrapper.scrollTop = this.scrollTop;
    }
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.fields.length > 0 && this.props.fields.length === 0) {
      this.setState({
        values: getInitValues(nextProps.fields, nextProps.fieldsValues),
        errors: {},
      });
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    if (this.props.ifSalesforceLoading !== nextProps.ifSalesforceLoading
      || this.props.disable !== nextProps.disable
      || this.props.salesforceSubmitError !== nextProps.salesforceSubmitError
      || !_.isEqual(this.state.values, nextState.values)
      || !_.isEqual(this.state.errors, nextState.errors)) {
      return true;
    }
    return false;
  }
  componentDidUpdate(prevProps, prevState) {
    if (prevProps.ifSalesforceLoading && !this.props.ifSalesforceLoading
      && this.props.salesforceSubmitError) {
      this.formWrapper.scrollTop = 0;
    }
    if (!_.isEqual(this.state.values, prevState.values)
      || !_.isEqual(this.state.errors, prevState.errors)) {
      localStorage.setObject('salesforceFormState', this.state);
    }
  }
  componentWillUnmount() {
    localStorage.set('salesforceFormScrollTop', this.formWrapper.scrollTop);
  }
  onSubmitSalesforce() {
    const e = defaultValidate(this.state.values, this.props);
    if (Object.keys(e).length === 0) {
      this.props.onSubmitSalesforceForm(this.state.values);
    } else {
      const name = Object.keys(e)[0];
      const errorElement = document.getElementById(`error_${name}`);
      if (errorElement) {
        if (errorElement.scrollIntoViewIfNeeded) errorElement.scrollIntoViewIfNeeded();
        else errorElement.scrollIntoView();
      }
    }
    this.setState({
      errors: e,
    });
  }
  onCancelClick() {
    localStorage.deleteItem('salesforceFormState');
    this.props.onCancelClick();
  }
  handleValueChange(name, value) {
    const e = Object.assign({}, this.state.errors);
    if (e[name]) {
      e[name] = '';
    }
    const temp = Object.assign({}, this.state.values);
    temp[name] = value;
    if (!_.isEqual(temp, this.state.values)) {
      this.setState({
        values: temp,
        errors: e,
      });
    }
  }
  render() {
    const props = this.props;
    return (
      <div className={style.relativeDiv}>
        { props.ifSalesforceLoading && <div className={style.loadingDiv} /> }
        <div ref={(element) => { this.formWrapper = element; }} className={style.formWrapper}>
          <div className={style.errorMessage} style={{ display: !props.ifSalesforceLoading && props.salesforceSubmitError ? 'block' : 'none' }}>
            <span>{props.salesforceSubmitError}</span>
          </div>
          <div>
            { props.fields.map((field, idx) => {
              if (field.type === fieldType.percent ||
              field.type === fieldType.currency || field.type === fieldType.double) {
                return (<RenderField
                  disable={props.disable} name={field.systemName} fieldInfo={field} key={idx}
                  input={{
                    value: this.state.values[field.systemName] || '',
                    onChange:
                      e =>
                        this.handleValueChange(field.systemName, normalizeDecimal(e.target.value)),
                  }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.int) {
                return (<RenderField
                  disable={props.disable} name={field.systemName} fieldInfo={field} key={idx}
                  input={{
                    value: this.state.values[field.systemName] || '',
                    onChange:
                      e => this.handleValueChange(field.systemName, normalizeInt(e.target.value)),
                  }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.date) {
                const dateV = this.state.values[field.systemName];
                let inputV;
                if (typeof dateV === 'string' && dateV) {
                  inputV = moment(convertSFDateToLocalDate(dateV));
                } else {
                  inputV = dateV;
                }
                return (<RenderField
                  disable={props.disable}
                  dateFormatter={props.dateFormatter}
                  name={field.systemName}
                  fieldInfo={field}
                  key={idx}
                  input={{ value: inputV,
                    onChange: value => this.handleValueChange(field.systemName, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.dateTime) {
                const dateTimeV = this.state.values[field.systemName];
                let inputTV;
                if (typeof dateTimeV === 'string' && dateTimeV) {
                  inputTV = moment(convertSFDateTimeToLocalDate(dateTimeV));
                } else {
                  inputTV = dateTimeV;
                }
                return (<RenderField
                  disable={props.disable}
                  dateFormatter={props.dateTimeFormatter}
                  name={field.systemName}
                  fieldInfo={field}
                  key={idx}
                  input={{ value: inputTV,
                    onChange: value => this.handleValueChange(field.systemName, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.reference) {
                return (<RenderField
                  disable={props.disable}
                  name={field.systemName}
                  fieldInfo={field}
                  key={idx}
                  onSalesforcelookUp={props.onSalesforcelookUp}
                  input={{ value: this.state.values[field.systemName] || '',
                    onChange: value => this.handleValueChange(field.systemName, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.bool) {
                return (<RenderField
                  disable={props.disable} name={field.systemName} fieldInfo={field} key={idx}
                  input={{ value: this.state.values[field.systemName] || false,
                    onChange: value => this.handleValueChange(field.systemName, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              } else if (field.type === fieldType.picklist ||
              field.type === fieldType.multiPicklist) {
                return (<RenderField
                  disable={props.disable} name={field.systemName} fieldInfo={field} key={idx}
                  input={{ value: this.state.values[field.systemName] || '',
                    onChange: value => this.handleValueChange(field.systemName, value) }}
                  meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
                />);
              }
              return (<RenderField
                disable={props.disable} name={field.systemName} fieldInfo={field} key={idx}
                input={{ value: this.state.values[field.systemName] || '',
                  onChange: e => this.handleValueChange(field.systemName, e.target.value) }}
                meta={{ touched: true, invalid: !!this.state.errors[field.systemName], error: this.state.errors[field.systemName] || '' }}
              />);
            })
          }
          </div>
        </div>
        <div className={style.buttonWrapper}>
          <Button
            onClick={this.onSubmitSalesforce}
            className={style.marginRight20}
            disabled={props.ifSalesforceLoading}
            loading={props.ifSalesforceLoading}
            iconPosition="right"
            text={props.ifSalesforceLoading ? 'Loading' : 'Save'}
            type="primary"
            htmlType="button"
          />
          <Button
            onClick={this.onCancelClick}
            text="Cancel"
            htmlType="button"
          />
        </div>
      </div>);
  }
}

CustomForm.propTypes = {
  className: React.PropTypes.string,
  visitorId: React.PropTypes.number,
  fieldsValues: React.PropTypes.arrayOf(React.PropTypes.shape({
    name: React.PropTypes.string,
    value: React.PropTypes.oneOfType([
      React.PropTypes.string,
      React.PropTypes.number,
      React.PropTypes.bool,
    ]),
  })),
  onSubmitSalesforceForm: React.PropTypes.func,
  onCancelClick: React.PropTypes.func,
  onSalesforcelookUp: React.PropTypes.func,
  fields: React.PropTypes.arrayOf(React.PropTypes.shape({
    label: React.PropTypes.string,
    required: React.PropTypes.bool,
    picklistValues: React.PropTypes.array,
    systemName: React.PropTypes.string,
    type: React.PropTypes.number,
    relatedObjects: React.PropTypes.array,
  })).isRequired,
  ifSalesforceLoading: React.PropTypes.bool,
  disable: React.PropTypes.bool,
  salesforceSubmitError: React.PropTypes.string,
  dateFormatter: React.PropTypes.string,
  dateTimeFormatter: React.PropTypes.string,
};

export default CustomForm;



